import { gsap } from 'gsap'

export default {
    name: 'HomeSlider',
   data() {
      return {
        posts: []
      }
    },
   
    methods: {
      onBeforeEnter,
      onEnter,
      onLeave
    }
    
}



function onBeforeEnter(el) {
  gsap.set(el, {
    scaleX: 0.25,
    scaleY: 0.25,
    opacity: 0
  })
}
  
function onEnter(el, done) {
  gsap.to(el, {
    duration: 1,
    scaleX: 1,
    scaleY: 1,
    opacity: 0.6,
    ease: 'back.out(2.5, 1)',
    onComplete: done
  })
  gsap.to(el, {
      duration: 1,
      translateY: 100,
      opacity: 1,
      delay: 1.5,
      ease: 'back.out(10,1)',
      onComplete: done
  })
   gsap.to(el, {
      duration: 1,
      translateY: 0,
      opacity: 1,
      delay: 2.5,
      ease: 'back.out(10,1)',
      onComplete: done
  })
}

function onLeave(el, done) {
	gsap.to(el, {
    duration: 0.7,
    scaleX: 1,
    scaleY: 1,
    x: 300,
    ease: 'elastic.inOut(2.5, 1)'
  })
  gsap.to(el, {
    duration: 0.2,
    delay: 0.5,
    opacity: 0,
    onComplete: done
  })

}





